.services__text {
  font-family: 'Noto Sans', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  color: #303030; }

.btn-span, .header__span-1 {
  font-family: 'Noto Sans', sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 25px; }

.header__span-2, .quote__text {
  font-family: 'Noto Sans', sans-serif;
  font-weight: 400;
  font-size: 42px;
  line-height: 57px; }

.services__subtitle, .tariff__block-right-text {
  font-family: 'Noto Sans', sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 25px;
  text-transform: uppercase;
  color: #303030; }

.services__title, .tariff__title {
  font-family: 'Noto Sans', sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 33px;
  color: #868585; }

.header__title {
  font-family: 'Noto Sans', sans-serif;
  font-weight: 700;
  font-size: 64px;
  line-height: 87px; }

.logo {
  margin-bottom: 140px;
  transition: 2s ease; }
  .logo:hover {
    transform: scale(1.5);
    transition: 0.1s; }
  .logo:hover #logo {
    fill: #196BAE; }

.btn-wrap {
  width: 308px;
  height: 100px;
  position: relative;
  margin-bottom: 250px; }
  @media screen and (max-width: 435px) {
    .btn-wrap {
      margin-bottom: 120px; } }

.btn {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: transparent;
  border: 1px solid #FFFFFF;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 2s ease; }
  .btn:hover {
    background: #A9A32A;
    transition: 0.1s; }

.btn-span {
  color: #FFFFFF;
  position: absolute;
  right: 0;
  bottom: 16px; }

.social-list {
  display: flex;
  align-items: center;
  gap: 20px;
  list-style-type: none; }

.header {
  max-width: 1930px;
  padding: 0 5px;
  background-image: url(./../images/header/header-bg.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }
  .header__container {
    max-width: 930px;
    margin: auto;
    padding: 70px 0 38px; }
  .header__inner {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .header__span-1 {
    color: #FFFFFF;
    text-align: center; }
  .header__title {
    font-variant: small-caps;
    color: #FFFFFF;
    margin-bottom: 3px;
    text-align: center; }
    @media screen and (max-width: 435px) {
      .header__title {
        font-size: 45px;
        line-height: 60px; } }
  .header__span-2 {
    color: #FFFFFF;
    margin-bottom: 50px;
    text-align: center; }
    @media screen and (max-width: 435px) {
      .header__span-2 {
        font-size: 35px;
        line-height: 50px; } }

.services__container {
  max-width: 1224px;
  margin: auto;
  padding: 150px 5px; }
  @media screen and (max-width: 1440px) {
    .services__container {
      padding: 75px 5px; } }

.services__title {
  padding-left: 14px;
  margin-bottom: 60px; }
  @media screen and (max-width: 1440px) {
    .services__title {
      text-align: center;
      padding-left: 0; } }

.services__content {
  display: flex;
  gap: 90px; }
  @media screen and (max-width: 1440px) {
    .services__content {
      flex-wrap: wrap;
      flex-direction: column;
      align-items: center;
      gap: 0; } }

.services__block-text {
  padding-left: 14px; }
  @media screen and (max-width: 1440px) {
    .services__block-text {
      padding-left: 0; } }

.services__subtitle {
  max-width: 455px;
  margin-bottom: 15px; }

.services__text {
  max-width: 510px;
  margin-bottom: 50px; }
  .services__text:last-of-type {
    margin-bottom: 35px; }

.services__image {
  max-width: 600px;
  width: 100%; }

.tariff__container {
  max-width: 1223px;
  margin: auto;
  padding: 150px 5px 164px; }

.tariff__title {
  margin-bottom: 60px; }

.tariff__content {
  display: flex;
  gap: 91px; }

.tariff__block-right {
  padding-right: 13px; }

.tariff__block-right-text {
  max-width: 650px;
  margin-bottom: 17px; }

.tariff__block-right-img {
  max-width: 650px;
  width: 100%; }

.quote {
  max-width: 1930px;
  padding: 0 5px;
  background-image: url(./../images/quote/quote-bg.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }
  .quote__container {
    max-width: 673px;
    margin: auto;
    padding: 128px 0 144px; }
  .quote__inner {
    display: flex;
    justify-content: center;
    align-items: center; }
  .quote__text {
    text-align: center;
    color: #FFFFFF; }
    @media screen and (max-width: 576px) {
      .quote__text {
        font-size: 27px;
        line-height: 40px; } }

.footer__top {
  background-color: #f6a839;
  margin-top: 5px; }

.footer__top-wrapper {
  max-width: 1720px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 80px 0 95px; }
